.button{
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
    width: 91px;
    height: 46px;  
    background-color: #1A6AE2;
    color: #FFFFFF;
    border-radius: 4px; 
    border: none;
}

.button__block{
    margin-top: 20px;
    margin-bottom: 20px;
    display: flex;
    justify-content: center;
}